.testi {
  background: url(./img/testimonials.png) no-repeat;
  background-size: cover;
  background-color: #4a3b4e;
  &__container {
    padding-top: 55px;
  }
  &__title {
    margin-bottom: 55px;
    font-size: 2.25em; //36px
    line-height: 1.333333333333333em; //48px
    @media screen and (min-width: 768px) {
      font-size: 2.625em; //42px
      line-height: 1em; //42px
      margin-bottom: 90px;
    }
  }
  &__text {
    text-align: center;
    font-family: $lato;
    font-weight: 400;
    font-size: 0.875em; //14px
    line-height: 1.5em; //21px
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: gray;
      position: absolute;
      bottom: -20px;
      left: 0;
      right: 0;
      margin: 0 auto;
    }
    @media screen and (min-width: 1200px) {
      max-width: 890px;
      margin: 0 auto;
    }
  }
  @media screen and (min-width: 768px) {
    padding-top: 90px;
  }
  @media screen and (min-width: 1200px) {
    padding-top: 110px;
  }
}

.testi__logos {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  .testi__logo {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    max-width: 270px;
    margin-bottom: 45px;
    .logo {
      margin-bottom: 30px;
    }
    &__text {
      text-align: center;
      font-family: $lato;
      font-weight: 700;
      font-style: italic;
      font-size: 1em; //16px
      line-height: 1.5em; //24px
      color: $white;
    }
    @media screen and (min-width: 768px) {
      min-width: 345px;
    }
  }
  @media screen and (min-width: 768px) {
    justify-content: space-between;
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 120px;
  }
}
