.features {
  overflow: hidden;
  &__container {
    padding-right: unset;
    position: relative;
    @media screen and (min-width: 1200px) {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      background-color: $bg-features;
      padding-top: 100px;
      padding-bottom: 30px;
    }
  }
  &__title {
    color: $features-title;
    font-size: 2.625em; //42px
    line-height: 1.047619047619048; //44px
    margin-bottom: 50px;
    color: $light-purple;
    @media screen and (min-width: 1200px) {
      order: 10;
      margin-right: 60px;
    }
  }
}

.features__blocks {
  .feature {
    display: flex;
    align-items: center;
    flex-direction: column;
    &:not(:last-child) {
      margin-bottom: 40px;
    }
    &__img {
      margin-bottom: 22px;
    }
    &__title-text {
      text-align: center;
      .feature__title {
        color: $features-title;
        margin-bottom: 15px;
        font-weight: 400;
      }
      .feature__text {
        font-size: 0.875em; //14px
        line-height: 1.5em; //21px
        color: $text;
      }
    }
    @media screen and (min-width: 768px) {
      flex-direction: unset;
      justify-content: space-between;
      max-width: 700px;
      &__title-text {
        text-align: left;
        max-width: 560px;
      }
    }
    @media screen and (min-width: 1200px) {
      max-width: 550px;
      &__title-text {
        max-width: 390px;
      }
    }
  }
}

.code {
  @media screen and (min-width: 768px) {
    float: right;
  }
  @media screen and (min-width: 1200px) {
    position: absolute;
    right: 0;
    top: 150px;
    width: 50%;
  }
  @media screen and (min-width: 1400px) {
    position: absolute;
    right: 0;
    top: 150px;
    width: unset;
  }
}
