//fonts

$montserrat: "Montserrat", sans-serif;

$lato: "Lato", sans-serif;

//colors

$white: #fff;

$light-purple: #8d81ac;

$dark-purple: #27262a;

$gray: #333333;

$text: #848e97;

$present-title: #635c73;

$features-title: #323232;

$price-card: #463048;

$bg-features: #f4f4f4;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: $montserrat;
}

.container {
  padding: 0 15px;
  position: relative;
  @media screen and (min-width: 1200px) {
    padding: 0 90px;
  }
}

a {
  text-decoration: none;
}

.button {
  background-color: $light-purple;
  border-radius: 40px;
  padding: 12px 15px;
  font-family: $montserrat;
  font-weight: 700;
  font-size: 0.9375em; //15px
  line-height: 1em; // 15px
  border: 0;
  cursor: pointer;
  min-width: 120px;
  color: $white;
}

.title {
  text-align: center;
  font-weight: 700;
  color: $white;
}

.text {
  text-align: center;
  font-family: $lato;
  font-weight: 500;
}

.post-text {
  text-align: center;
  font-weight: 400;
  color: $text;
  font-size: 0.875em; //14px
  line-height: 1em; //14px
}
