.header {
  background: url(./img/comp.png) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
  position: relative;
  &__top {
    background-color: $dark-purple;
    padding: 31px 0;
    position: relative;
    margin-bottom: 50px;
    @media screen and (min-width: 768px) {
      padding: 20px 0;
      margin-bottom: 30px;
    }
    @media screen and (min-width: 1200px) {
      padding: 30px 0;
      background-color: unset;
    }
  }
  &__bot {
    padding: 0 10px;
    &__title {
      font-weight: 700;
      font-size: 3em; //48px
      line-height: 1em; //48px
      margin-bottom: 48px;
      margin: 0 0 19px 0;
      @media screen and (min-width: 768px) {
        font-size: 4.5em; //72px
      }
      @media screen and (min-width: 1200px) {
        font-size: 5.125em; //82px
      }
    }
    &__text {
      text-align: center;
      color: $light-purple;
      font-weight: 400;
      font-size: 1.125em; //18px
      line-height: 1.333333333333333em; //24px
      margin-bottom: 14px;
      @media screen and (min-width: 768px) {
        font-size: 1.5em; //24px
        line-height: 1em; //24px
      }
      @media screen and (min-width: 1200px) {
        font-size: 1.875em; //30px
      }
    }
  }
  .download-btn-container {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(65%);
    .download-btn {
      display: block;
      margin: 0 auto;
      margin-bottom: 10px;
      @media screen and (min-width: 768px) {
        padding: 15px 20px;
      }
    }
  }
}

.d-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media screen and (min-width: 768px) {
    flex-wrap: wrap;
  }
  @media screen and (min-width: 1200px) {
    flex-wrap: unset;
  }
}

.btn-buy {
  @media screen and (min-width: 768px) {
    min-width: 150px;
  }
  @media screen and (min-width: 1200px) {
    order: 10;
    margin: unset;
  }
}

.logo {
  display: flex;
  align-items: center;
  &__text {
    font-family: $lato;
    font-weight: 700;
    text-transform: uppercase;
    color: $light-purple;
    font-size: 1.5em; //24px
    line-height: 0.666666666666667; //16px
    margin-left: 15px;
    display: none;
    @media screen and (min-width: 768px) {
      display: unset;
    }
  }
}

.checkbox {
  display: none;
}

label {
  @media screen and (min-width: 768px) {
    display: none;
  }
}

.burger {
  width: 26px;
  height: 21px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  overflow: hidden;
  .line {
    display: block;
    background-color: $white;
    border-radius: 5px;
    height: 3px;
    width: 100%;
    transition: all 0.3s ease-in-out;
    &:nth-child(2) {
      margin: 6px 0;
    }
    &:first-child {
      width: 15px;
    }
  }
  @media screen and (min-width: 768px) {
    display: none;
  }
}

.checkbox:checked + label > .burger > .line:first-child {
  transform: translateX(-50px);
  transition: all 0.4s;
}

.checkbox:checked + label > .burger > .line:nth-child(2) {
  transform: rotate(45deg);
  transition: all 0.4s;
}

.checkbox:checked + label > .burger > .line:last-child {
  transform: translate(0, -9px) rotate(-45deg);
  transition: all 0.4s;
}

.nav {
  display: none;
  flex-direction: column;
  min-width: 97%;
  position: absolute;
  bottom: -50%;
  transform: translateY(100%);
  background-color: $gray;
  font-family: $montserrat;
  font-weight: 400;
  font-size: 1.625em; //26px
  line-height: 1em; //26px
  &__link {
    color: $white;
    padding: 25px 0 25px 40px;
    position: relative;
    &:not(:last-child) {
      border-bottom: 3px solid #3b3b3b;
    }
    &:active,
    &:hover {
      color: $light-purple;
      &::after {
        content: "";
        display: block;
        width: 8px;
        height: 53px;
        background-color: $light-purple;
        position: absolute;
        left: 2px;
        top: 12px;
      }
    }
  }
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: unset;
    background-color: unset;
    position: unset;
    width: 100%;
    transform: unset;
    justify-content: space-evenly;
    min-width: unset;
    margin-top: 20px;
    .nav__link {
      border: unset;
      color: $light-purple;
      padding: unset;
      font-size: 0.615384615384615em; //16px
      line-height: 1.125em; //18px
      &:active,
      &:hover {
        color: $white;
        &::after {
          display: none;
        }
      }
    }
  }
  @media screen and (min-width: 1200px) {
    margin-left: auto;
    margin-top: unset;
    .nav__link {
      margin-right: 45px;
    }
  }
}

.checkbox:checked ~ .nav {
  display: flex;
}
