.price {
  background: url(./img/price.png) no-repeat;
  background-size: cover;
  &__container {
    padding-top: 120px;
    @media screen and (min-width: 768px) {
      padding: 100px;
    }
    @media screen and (min-width: 1200px) {
      padding: 120px;
    }
  }
  &__title {
    margin-bottom: 32px;
    font-size: 1.75em; //28px
    line-height: 1.5em; //42px
    @media screen and (min-width: 768px) {
      font-size: 2.625em; //42px
      margin-bottom: 60px;
    }
    @media screen and (min-width: 1200px) {
      font-size: 3em; //48px
    }
  }
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  .card {
    width: 257px;
    min-height: 457px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 20px;
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: $white;
      opacity: 0.11;
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100px;
      background-color: $white;
      opacity: 0.06;
      top: 0;
    }

    &__title {
      margin-top: 40px;
      font-size: 1.5em; //24px
      line-height: 1em; //24px
      margin-bottom: 30px;
    }
    &__coast {
      font-size: 3.75em; //60px
      line-height: 1em; //60px
      font-weight: 700;
    }
    &__how {
      font-size: 0.875em; //14px
      line-height: 1em; //14px
    }
    &__line {
      display: block;
      width: 60px;
      height: 5px;
      background-color: $light-purple;
    }
    &__info {
      font-size: 1em; //16px
      line-height: 1em; //16px
      text-transform: none;
      text-align: center;
    }
    &__info__sec {
      margin-bottom: 37px;
    }
    &__button {
      font-size: 0.9375em; //15px
      line-height: 1em; //15px
      padding: 15px 40px;
      text-transform: uppercase;
      font-weight: 400;
      background-color: transparent;
      position: relative;
      margin-bottom: 30px;
      &::before {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        background-color: $white;
        opacity: 0.15;
        position: absolute;
        left: 0;
        right: 0;
        margin: 0 auto;
        top: 0;
        border-radius: inherit;
      }
    }
    @media screen and (min-width: 768px) {
      &:nth-child(3) {
        transform: translateX(-10%);
      }
    }
    @media screen and (min-width: 1200px) {
      &:nth-child(3) {
        transform: unset;
      }
    }
  }
  .agency {
    width: 300px;
    min-height: 560px;
  }

  .card:nth-child(3) {
    .txt {
      z-index: 10;
    }
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: #463048;
      opacity: 0.6;
      z-index: 1;
    }
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100px;
      background-color: #000;
      opacity: 0.06;
      top: 0;
      z-index: 1;
    }
  }
  @media screen and (min-width: 768px) {
    justify-content: space-between;
  }
  @media screen and (min-width: 1024px) {
    justify-content: space-around;
  }
  @media screen and (min-width: 1200px) {
    justify-content: space-between;
  }
}
