.editor {
  padding: 125px 10px;
  &__title {
    font-size: 1.625em; //26px
    font-weight: 1.076923076923077em; //28px
    color: $present-title;
    margin-bottom: 27px;
    @media screen and (min-width: 768px) {
      font-size: 2.625em; //42px
      line-height: 1em; //42px
    }
    @media screen and (min-width: 1200px) {
      font-size: 3.375em; //54px
      line-height: 1em; //54px
    }
  }
  &__text {
    font-size: 0.875em; //14px
    font-weight: 1.428571428571429em; //20px
    color: $features-title;
    margin-bottom: 36px;
    @media screen and (min-width: 768px) {
      font-size: 1.3125em; //21px
      line-height: 1.523809523809524em; //32px
    }
  }
}

.watches {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  .watch {
    min-height: 110px;
    min-height: 26px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $text;
    border-radius: 4px;
    &__text {
      background-color: #f6f6f6;
      padding: 3px;
      display: flex;
      justify-content: center;
      border-right: 1px solid $text;
      gap: 3px;
      font-weight: 700;
    }
    &__nums {
      padding: 3px;
      font-size: 0.8em;
    }
  }
}
